





















import { Component, Prop, Vue } from "vue-property-decorator";
import { db } from "../plugins/firebase";

const images = db.collection("images");

@Component
export default class ImageViewer extends Vue {
  comments: any[] = [];
  images: any[] = [];
  swiperOption = {
    slidesPerView: "auto",
    centeredSlides: true,
    spaceBetween: 50,
    loop: true
  };
  timer: number = 0;
  index: number = 0;
  showImageurl: string | null = null;
  changeTime: number = 8000;

  created() {
    db.collection("images")
      .orderBy("created_at")
      .onSnapshot(snapShot => {
        clearTimeout(this.timer);
        let docs: any[] = [];
        snapShot.forEach(doc => docs.push(doc.data()));
        this.images = docs;
        this.showImageurl = this.images[0]["url"] || null;
        this.onTimer();
      });
    db.collection("comments")
      .orderBy("created_at")
      .onSnapshot(snapShot => {
        let docs: any[] = [];
        snapShot.forEach(doc => {
          const exists: boolean =
            this.comments.some(comment => {
              console.log(doc.id + ":" + comment.id);
              return comment.id === doc.id;
            }) || false;
          if (!exists) {
            this.comments.push(
              Object.assign(doc.data(), { y: this.getPosition(), id: doc.id })
            );
          }
        });
      });
  }
  onTimer() {
    this.timer = setTimeout(() => {
      const nextIndex: number =
        this.images.length > this.index + 1 ? this.index + 1 : 0;
      this.showImageurl = this.images[nextIndex]["url"];
      this.index = nextIndex;
      this.onTimer();
    }, this.changeTime);
  }
  getPosition() {
    const max: number = 10;
    const min: number = window.outerHeight - 50;
    return Math.floor(Math.random() * (max + 1 - min)) + min;
  }
  mounted() {
    this.onTimer();
  }
}
